import React from 'react';
import ReactStreetview from 'react-streetview';
import Geocode from "react-geocode";

class BuildingView extends React.Component {
    render() {
        // see https://developers.google.com/maps/documentation/javascript
        const googleMapsApiKey = 'AIzaSyCwIG4lbTXNcDjbNAetvu3XMiiUYJm78Tc';

        // see https://developers.google.com/maps/documentation/javascript/3.exp/reference#StreetViewPanoramaOptions
        const streetViewPanoramaOptions = {
            position: {lat: this.props.building.location[0], lng: this.props.building.location[1]},
            pov: {heading: 45, pitch: 0},
            zoom: 1
        };
 
        return (
            <div style={{
                width: '1000px',
                height: '500px',
                backgroundColor: '#eeeeee'
            }}>
                <ReactStreetview
                    apiKey={googleMapsApiKey}
                    streetViewPanoramaOptions={streetViewPanoramaOptions}
                />
            </div>
        );
    }
}

export default BuildingView