import React from 'react'
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';
import './Simple.css'

const options = [
    'Mursten', 'Beton', 'Træ'
];

class Simple extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            Material: 'asd'
        }

        this.handleSelect = this.handleSelect.bind(this)
    }

    handleSelect(newMaterial) {
        this.setState({Material: newMaterial.value})
    }

    render() {
        if (this.props.building.selectAddress === true && this.props.building.selectEdit === 'simple') {
            return(
                <div className='simpleMain'>
                    <h2>REDIGER SIMPEL</h2>
                    <div className='dropdownSimple'>
                        <Dropdown options={options} onChange={this.handleSelect} placeholder="Select an option" />
                        <a>{this.state.Material}</a>
                    </div>
                    <div className='buttonContainer'>
                        <div className="divButton">
                            <a className="buttonMaterial" onClick={this.handleClick}>Tilbage</a>
                        </div>
                    </div>
                </div>
            )
        } else {
            return(
                <div></div>
            )
        }
    }
}

export default Simple