import React from 'react'
import './Advanced.css'

class Advanced extends React.Component {
    constructor(props) {
        super(props)

        this.handleClick = this.handleClick.bind(this)
    }

    handleClick() {
        const edit = ''
        this.props.onChange(edit)
    }

    render() {
        if (this.props.building.selectAddress === true && this.props.building.selectEdit === 'advanced') {
            return(
                <div className='advancedMain'>
                    <h2>REDIGER AVANCERET</h2>
                    <div className='buttonContainer'>
                        <div className="divButton">
                            <a className="buttonMaterial" onClick={this.handleClick}>Tilbage</a>
                        </div>
                    </div>
                </div>
            )
        } else {
            return(
                <div></div>
            )
        }
    }
}

export default Advanced