import React from 'react'
import { ThemeConsumer } from 'styled-components';
import './Material.css'

class Material extends React.Component {
  constructor(props) {
    super(props);

    this.handleSimpleClick = this.handleSimpleClick.bind(this);
    this.handleAdvancedClick = this.handleAdvancedClick.bind(this);
  }

  handleSimpleClick() {
    const edit = 'simple'
    this.props.onChange(edit)
  }

  handleAdvancedClick() {
    const edit = 'advanced'
    this.props.onChange(edit)
  }

  render() {
    if (this.props.building.selectAddress === true && this.props.building.selectEdit === '') {
    return(
      <div className="mainMaterial">
        <div className="buttonContainer">
          <div className="divButton">
            <a className="buttonMaterial" onClick={this.handleSimpleClick}>Rediger</a>
            <a className="buttonMaterial" onClick={this.handleAdvancedClick}>Rediger avanceret</a>
          </div>
        </div>
      </div>
    )
    } else {
      return(
        <div></div>
      )
    }
  }
}
export default Material