import React from 'react';
import './Building.css';
import BuildingView from '../BuildingView/BuildingView'

class Building extends React.Component {
  constructor(props) {
    super(props)
  }
  render() {
    if (this.props.building.selectAddress === true && this.props.building.selectEdit === '') {
    return (
      <div className="Building">
        <div className="image-container">
          {/*<BuildingView building={this.props.building}/>*/}
          <img src={this.props.building.imageSrc}></img>
        </div>
        <h2>{this.props.building.address}</h2>
        <div className="adress-information">
          <div className="Building-address">
            <p>{this.props.building.area}</p>
            <p>{this.props.building.floors}</p>
          </div>
        </div>
      </div>
    )
    } else {
      return(
        <div></div>
      )
    }
  }
}
export default Building;